import {forwardRef} from 'react';
import styles from './Frame.module.scss';
import Shape from '../Shape/Shape';

interface Props {
    isElements: number[][]
    onToggleShape: any,
}

const Frame = forwardRef<HTMLDivElement, Props>(({isElements, onToggleShape}, frameElement ) => {
    return (
        <div ref={frameElement} className={styles['frame']} onClick={onToggleShape}>
            {isElements.map((shapes, i: number) => {
                return (
                    <div className={styles['frame__row']} key={`${i}_frame_row`}>
                        {shapes.map((_, j: number ) => {
                            return (
                                <Shape id={`${i}_${j}`} isActive={!!isElements[i][j]} key={`${i}_${j}_shape`} />
                            );
                        })}
                    </div>
                );
            })}
        </div>
    );
});
export default Frame;
